ipalmente por el uso no sostenible de este en los sectores agropecuarios, industriales y mineros. Las principales amenazas para el suelo son: la erosión, la salinización, la pérdida de carbono orgánico, contaminación de los suelos, pérdida de la biodiversidad y compactación que inevitablemente llevan a procesos de desertificación.")]),_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"zoom-in"}},[_c('TabsA',{staticClass:"color-acento-contenido"},[_c('div',{staticClass:"row justify-content-center BGR08 p-4",attrs:{"titulo":"Factores biodegradantes <br>del suelo"}},[_c('div',{staticClass:"col-lg-7 mb-lg-0 mb-4"},[_c('h4',[_vm._v("Factores biodegradantes del suelo")]),_c('p',[_vm._v("La desertificación es definida como la disminución irreversible de la productividad de un ecosistema, a causa de malas prácticas en zonas especialmente secas, lo cual se va representado en la pérdida de los servicios ecosistémicos provistos por el suelo y aumento de la aridez. Estas malas prácticas están relacionadas con la deforestación, la extensión de infraestructura agrícola, la ganadería, los cultivos intensivos, el riego y los factores climáticos.")])]),_c('div',{staticClass:"col-lg-5"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img46.svg")}})])]),_c('div',{staticClass:"row justify-content-center BGR08 p-4",attrs:{"titulo":"Riesgos y tratamientos"}},[_c('div',{staticClass:"col-lg-7 mb-lg-0 mb-4"},[_c('h4',[_vm._v("Riesgos y tratamientos")]),_c('p',[_vm._v("Las afectaciones generadas al suelo están relacionadas con el crecimiento demográfico y el uso de la tierra. Entre las estrategias propuestas para solventar estas afectaciones está la restauración de la productividad del suelo con técnicas de manejo e incremento de fertilidad, cambio en prácticas agrícolas e inclusión de tecnología para mejorar el rendimiento de cultivos, promoción de prácticas agrícolas apropiadas y gestión de tierras sostenibles en las áreas cultivables y, el uso eficiente de in